/** 
 * N.B.
 * _variables.scss must be imported first so as to 
 * be available to all other files,
 * _base.scss, layout.scss should be last for a similar reason:
 * they consumes all other style sheets
*/
  
/*  ******
----------
FONTS
----------
******  */
/// sans-serif
/// @type String
@import url('https://fonts.googleapis.com/css?family=Assistant:300,400,700&display=swap');
$font-family-sans-serif:    'Assistant', Arial, sans-serif !default;
/// serif
/// @type String
@import url('https://fonts.googleapis.com/css?family=Playfair+Display:400,700&display=swap');
$font-family-serif:         'Playfair Display', Courier, serif !default;
/// cursive
/// @type String
@import url('https://fonts.googleapis.com/css?family=Lovers+Quarrel&display=swap');
$font-family-cursive:       'Lovers Quarrel', cursive !default;

/// @type Map
$loris-font-families: (
  "sans-serif": $font-family-sans-serif,
  "serif": $font-family-serif,
  "cursive": $font-family-cursive
) !default;

/// @type Map
$font-families: () !default;

/// @type Map
$theme-font-families: map-merge($loris-font-families, $font-families) !default;

/// @type Number
$font-weight-light:         300 !default;
/// @type Number
$font-weight-normal:        400 !default;
/// @type Number
$font-weight-heavy:         700 !default;

/// @type Map
$loris-font-weights: (
  "light": $font-weight-light,
  "normal": $font-weight-normal,
  "heavy": $font-weight-heavy
) !default;

/// @type Map
$font-weights: () !default;

/// @type Map
$theme-font-weights: map-merge($loris-font-weights, $font-weights) !default;

/*  ******
----------
TYPOGRAPHY
----------
******  */

/// @type Number
$font-size-base:            1rem !default;
/// @type Number
$font-size-lead:            $font-size-base * 1.3 !default;
/// @type Number
$font-size-small:           $font-size-base * .8 !default;

/// setup map for headings sizes
/// @type Map
$font-sizes-headings: (
  "h1": $font-size-base * 2,
  "h2": $font-size-base * 1.8,
  "h3": $font-size-base * 1.5,
  "h4": $font-size-base * 1.3,
  "h5": $font-size-base * 1.1,
  "h6": $font-size-base * 1
) !default;

/// @type Map
$loris-font-sizes: (
  "base": $font-size-base,
  "lead": $font-size-lead,
  "small": $font-size-small
) !default;

/// @type Map
$font-sizes: () !default;

/// @type Map
$theme-font-sizes: map-merge($loris-font-sizes, $font-sizes) !default;

/*  ******
----------
COLOURS
----------
******  */

/// @type Color
$color--main:               #71BC78 !default;
/// @type Color
$color--complementary:      #BC7871 !default;
/// @type Color
$color--secondary:          #779AAD !default;
/// @type Color
$color--dark:               #3E403F !default;
/// @type Color
$color--very-dark:          #2B2C2C !default;
/// @type Color
$color--mid:                #B1B2B2 !default;
/// @type Color
$color--light:              #FFFFFF !default;

/// @type Color
$black-10:                  rgba(0, 0, 0, .1);
/// @type Color
$black-20:                  rgba(0, 0, 0, .2);
/// @type Color
$black-30:                  rgba(0, 0, 0, .3);
/// @type Color
$black-40:                  rgba(0, 0, 0, .4);
/// @type Color
$black-50:                  rgba(0, 0, 0, .5);
/// @type Color
$black-60:                  rgba(0, 0, 0, .6);
/// @type Color
$black-70:                  rgba(0, 0, 0, .7);
/// @type Color
$black-80:                  rgba(0, 0, 0, .8);
/// @type Color
$black-90:                  rgba(0, 0, 0, .9);

/// @type Map
$loris-colors: (
  "main": $color--main,
  "complementary": $color--complementary,
  "secondary": $color--secondary,
  "dark": $color--dark,
  "very-dark": $color--very-dark,
  "mid": $color--mid,
  "light": $color--light
) !default;

/// @type Map
$colors: () !default;

/// @type Map
$theme-colors: map-merge($loris-colors, $colors) !default;

/*  ******
----------
SHADOWS
----------
******  */
/// @type String
$base-shadow:               0 0 6px 0 $black-50 !default;
/// @type String
$shadow-right:              5px 5px 6px 0 $black-50 !default;
/// @type String
$shadow-left:               -5px 5px 6px 0 $black-50 !default;
/// @type String
$shadow-bottom:             0 3px 6px 0 $black-50 !default;
/// @type String
$shadow-top:                0 -3px 6px 0 $black-50 !default;

/// @type Map
$loris-shadows: (
  "base": $base-shadow,
  "right": $shadow-right,
  "left": $shadow-left,
  "bottom": $shadow-bottom,
  "top": $shadow-top
) !default;

/// @type Map
$shadows: () !default;

/// @type Map
$theme-shadows: map-merge($loris-shadows, $shadows) !default;

/*  ******
----------
LAYOUT
----------
******  */

// ------- BREAKPOINTS

/// @type Number
$breakpoint-sm:             576px !default;
/// @type Number
$breakpoint-md:             767px !default;
/// @type Number
$breakpoint-lg:             992px !default;
/// @type Number
$breakpoint-xl:             1248px !default;

/// @type Map
$loris-breakpoints: (
  "sm": $breakpoint-sm,
  "md": $breakpoint-md,
  "lg": $breakpoint-lg,
  "xl": $breakpoint-xl
) !default;

/// @type Map
$breakpoints: () !default;

/// @type Map
$theme-breakpoints: map-merge($loris-breakpoints, $breakpoints) !default;

// ------- ZINDEX

/// @type Nubmer
$zindex-foreground:         500 !default;
/// @type Number
$zindex-behind:             -10 !default;
/// @type Number
$zindex-background:         -500 !default;

/// @type Map
$loris-zindex: (
  "foreground": $zindex-foreground,
  "behind": $zindex-behind,
  "background": $zindex-background
) !default;

/// @type Map
$zindex: () !default;

/// @type Map
$theme-zindex: map-merge($loris-zindex, $zindex) !default;

/*
 *  ----------
 *  BREAKPOINTS
 *  ----------
*/

/// short hand for using media queries at a pre-defined breakpoint
///
/// @content
/// @author stephen pal-george
/// @group layout
/// @require $theme-breakpoints
///
/// @param {string} $size - one from the $theme-breakpoints map
///
@mixin breakpoint($size) {
  @media (min-width: map-get($theme-breakpoints, $size)) {
    @content;
  }
}

/*
 *  ----------
 *  GRID SYSTEM
 *  ----------
*/

/// define width of an element based on 12-column grid system
///
/// @author stephen pal-george
/// @group layout
///
/// @param {number} $cols - number of columns the element should cover
///
@mixin set-grid-span($cols) {
  width: (100% / 12) * $cols;
}

/// define x-axis offset of an element, based on 12-column grid system
///
/// @author stephen pal-george
/// @group layout
///
/// @param {number} $cols - number of columns the element should be offset by
/// @param {string} $type - how should the element be positioned (one of ['margin', 'position'])
@mixin set-offset-span($cols, $type: 'margin') {
  @if $type == 'margin' {
    margin-left: (100% / 12) * $cols;
  }
  @else if $type == 'position' {
    left: (100% / 12) * $cols;
  }
  @else {
    margin-left: (100% / 12) * $cols;
  }
}

/*
 *  ----------
 *  TYPOGRAPHY
 *  ----------
*/

/// styles text with a coloured backgorund
///
/// @author stephen pal-george
/// @group typography
///
/// @param {string} $color - one from the $theme-colors map
/// @param {number} $padding-x - padding-left & padding-right value in rems
/// @param {number} $padding-y - padding-top & padding-bottom value in rems
/// @param {number} $line-height
///
@mixin padded-text($color, $padding-x, $padding-y, $line-height) {
  display: inline;
  box-decoration-break: clone;
  background: $color;
  padding: #{$padding-x}rem #{$padding-y}rem;
  line-height: $line-height;
}
/// setup classes for adding shadows
/// @require $theme-shadows
/// @see './variables'
///
@each $shadowType, $shadowValue in $theme-shadows {
  .shadow--#{$shadowType} {
      box-shadow: $shadowValue;
  }
}
@each $color, $hex in $theme-colors {
  %color--#{$color} {
    color: $hex;
  }
  .color--#{$color} {
    @extend %color--#{$color};
  }

  %bg--#{$color} {
    background-color: $hex;
  }
  .bg--#{$color} {
    @extend %bg--#{$color};
  }
}
/*
 *  ----------
 *  GRID SYSTEM
 *  ----------
*/
@for $i from 1 through 12 {
  $value: (100% / 12) * $i;
  %grid-span-#{$i} {
    width: $value;
    flex-basis: $value;
  }
  .grid-span-#{$i} {
    @extend %grid-span-#{$i};
  }

  %offset-span-#{$i} {
    margin-left: $value;
  }
  .offset-span-#{$i} {
    @extend %offset-span-#{$i};
  }
}

.column {
  display: flex;
  flex-direction: column;
}
.row {
  display: flex;
  flex-direction: row;
}
/*
 *  ----------
 *  CONTAINERS
 *  ----------
*/
.container--full-width {
    width: 100%;
    padding: 3rem 2rem;
    margin-left: auto;
    margin-right: auto;
    overflow: auto;
  
    display: flex;
    flex-wrap: wrap;
  }
  
  /// @require $theme-breakpoints
  /// @see 'variables.scss'
  .container {
    max-width: map-get($map: $theme-breakpoints, $key: "xl");
    padding: 2rem;
    margin-left: auto;
    margin-right: auto;
    overflow: auto;
  
    display: flex;
    flex-wrap: wrap;
      
    @include set-grid-span(12);
    @include breakpoint("md") {
      @include set-grid-span(10);
    }
  }
  
  
  %text-container--narrow {
    margin: 0 auto;
    @include set-grid-span(10);
    
    @include breakpoint("md") {
      @include set-grid-span(8);
    }
  }
  .text-container--narrow {
    @extend %text-container--narrow;
  }
  
  %text-container--very-narrow {
    margin: 0 auto;
    @include set-grid-span(10);
    
    @include breakpoint("md") {
      @include set-grid-span(8);
    }
    @include breakpoint("lg") {
      @include set-grid-span(6);
    }
  }
  .text-container--very-narrow {
    @extend %text-container--very-narrow;
  }
  
  /*
   *  ----------
   *  SPACING
   *  ----------
  */
  
  /// ------- PADDING
  
  %no-padding-x {
    padding-left: 0 !important;
    padding-right: 0 !important;
  }
  .no-padding-x {
    @extend %no-padding-x;
  }
  
  %no-padding-y {
    padding-top: 0 !important;
    padding-bottom: 0 !important;
  }
  .no-padding-y {
    @extend %no-padding-y;
  }
  
  .no-padding {
    @extend %no-padding-x;
    @extend %no-padding-y;
  }
  
  %white-space-y {
    padding-top: 5rem !important;
    padding-bottom: 5rem !important;
  }
  .white-space-y {
    @extend %white-space-y;
  }

  %white-space-x {
    padding-left: 5rem !important;
    padding-right: 5rem !important;
  }
  .white-space-x {
    @extend %white-space-x;
  }

  .white-space {
    @extend %white-space-x;
    @extend %white-space-y;
  }
  
  /// ------- MARGIN
  
  %no-margin-x {
    margin-left: 0 !important;
    margin-right: 0 !important;
  }
  .no-margin-x {
    @extend %no-margin-x;
  }
  
  %no-margin-y {
    margin-top: 0 !important;
    margin-bottom: 0 !important;
  }
  .no-margin-y {
    @extend %no-margin-y;
  }
  
  .no-margin {
    @extend %no-margin-x;
    @extend %no-margin-y;
  }

  /*
   *  ----------
   *  ALIGNMENT
   *  ----------
  */
  
  .children--justify-start {
    display: flex;
    justify-content: flex-end;
  }

  .children--justify-centre, .children--justify-center {
    display: flex;
    justify-content: center;
  }

  .children--justify-end {
    display: flex;
    justify-content: flex-start;
  }

  .children--align-start {
    display: flex;
    align-items: flex-start;
  }

  .children--align-centre, .children--align-center {
    display: flex;
    align-items: center;
  }

  .children--align-end {
    display: flex;
    align-items: flex-end;
  }

  .children--align-stretch {
    display: flex;
    align-items: stretch;
  }

  /*
   *  ----------
   *  ZINDEX
   *  ----------
  */

  @each $index, $value in $theme-zindex {
    %zindex--#{$index} {
      z-index: $value;
    }
    .zindex--#{$index} {
      @extend %zindex--#{$index};
    }
  }

/// use map $font-sizes-headings to generate classes that can 
/// force font size changes on any element
/// @require $font-size-headings
///
@each $tag, $size in $font-sizes-headings {
  .text--#{$tag} {
    font-size: $size;
  }
}

/// @type Map
///
$font-sizes-display: (
  "site-title": $font-size-base * 3,
  "page-title": $font-size-base * 2.4
) !default;

/// use map $font-sizes-display to generate classes that can 
/// force font size changes on any element
/// @require $font-sizes-display
///
@each $name, $size in $font-sizes-display {
  .text--#{$name} {
    font-size: $size;
  }
}

/// @see './variables.scss'
/// @require $theme-font-sizes
/// $theme-font-sizes includes: 
/// ["base", "lead", "small"]
@each $size, $value in $theme-font-sizes {
  .text--#{$size} {
    font-size: $value;
  }
}

/// @see './variables.scss
/// @require $theme-font-families
/// $theme-font-families includes:
/// ["sans-serif", "serif", "cursive"]
@each $family, $name in $theme-font-families {
  .font-family--#{$family} {
    font-family: $name;
  }
}

/// @see './variables.scss'
/// @require $theme-font-weights
/// $theme-font-weights includes:
/// ["light", "normal", "heavy"]
@each $weight, $value in $theme-font-weights {
  .font-weight--#{$weight} {
    font-weight: $value;
  }
}

/// @see './variables.scss'
/// @require $theme-colors
/// $theme-colors includes:
/// ["main", "complementary", "secondary", "dark", "light"]
@each $color, $hex in $theme-colors {
  .title__padded-text--#{$color} {
    @include padded-text($hex, .6, 1, 1.5);
  }

  .body__padded-text--#{$color} {
    @include padded-text($hex, .4, .8, 1.6);
  }
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;

  &::before,
  &::after {
    box-sizing: border-box;
  }
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  min-height: 100vh;
  scroll-behavior: smooth;
  text-rendering: auto;
}

 ul[class],
 ol[class] {
   list-style-type: none;
 }

/*  ******
----------
STRUCTURE
----------
******  */
section, div {
  overflow: auto;
}

/*  ******
----------
HEADINGS
----------
******  */
/// @require $font-sizes-headings
/// @see './variables.scss'
///
@each $tag, $size in $font-sizes-headings {
  #{$tag} {
    font-size: $size;
  }
}

/// @require $font-family-serif
/// @see './variables.scss'
///
h1, h2,
h3, h4,
h5, h6 {
  font-family: $font-family-serif;
}

/*  ******
----------
OTHER TEXT
----------
******  */
/// @require $font-size-base, @require $font-family-sans-serif
/// @see './variables.scss'
///
p {
  font-size: $font-size-base;
  font-family: $font-family-sans-serif;
}

a {
  display: inline-block;
}

@media (prefers-reduced-motion: reduce) {
  * {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}